<template>
  <main id="cont">
    <section class="login-client-pg">
      <div class="login-client-container calc">
        <div class="w-100">
          <div class="login-title-box">
            <h2 class="login-title-txt"></h2>
          </div>
          <form action="" class="login-client-form">
            <!--  -->
            <div class="form-label-box">
              <input type="email" id="loginEmail" class="form-input" placeholder=" " autofocus />
            </div>
            <!--  -->
            <div class="form-label-box">
              <input type="password" id="loginPassword" class="form-input" placeholder=" " />
            </div>
            <!--  -->
            <div class="standard-btn-box">
              <button class="standard-btn pointer" type="submit" @click="loginClick()"></button>
            </div>
          </form>
          <div class="pg-more-area">
            <a href="#"> </a>
            <span></span>
            <a href="#"> </a>
            <span></span>
            <a href="/signUp"></a>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>
<script>
export default {
  name: 'Login',
  data() {
    return {};
  },
  methods: {
    loginClick() {
      let userEmail = document.querySelector('#loginEmail');
      let userPw = document.querySelector('#loginPassword');

      event.preventDefault();

      //   
      if (userEmail.value == '') {
        alert('  .');
      }

      //   
      else if (userPw.value == '') {
        alert('  .');
      }

      // ,    
      if (userEmail.value != '' && userPw.value != '') {
        if (localStorage.getItem('join') == null) {
          alert('  .');
          userEmail.value = '';
          userPw.value = '';
        }
        //   
        if (localStorage.getItem('join') != null) {
          let infoGet = JSON.parse(localStorage.getItem('join'));
          if (
            infoGet.findIndex((a) => {
              return a['email'] == userEmail.value;
            }) == -1
          ) {
            alert('  .');
            userEmail.value = '';
          } else {
            //   
            if (
              infoGet.findIndex((a) => {
                return a['password'] == userPw.value;
              }) == -1
            ) {
              alert('  .');
              userPw.value = '';
            }
            //  
            else {
              alert(
                infoGet[
                  infoGet.findIndex((a) => {
                    return a['email'] == userEmail.value;
                  })
                ]['name'] + ', TAMBURINS   !'
              );

              //  
              document.cookie = 'login=true; expires';

              //  
              // window.location.href = 'http://localhost:8080';
              this.$router.push({ path: '/' });
            }
          }
        }
      }
    },
  },
};
</script>
<style>
@import '../../assets/css/sub/login.css';
</style>
