<template>
  <main id="cont">
    <div class="cont">
      <div class="prd-detail-wrap">
        <!--    -->
        <div class="detail-prd-img-bx">
          <div v-for="(detailImg, i) in prdData[num]['img']['detailImg']" :key="i">
            <!--   -->
            <img
              class="detail-img"
              v-if="prdData[num]['img']['file'] == 'jpg'"
              :src="require(`../../assets/image/prd/detail/${detailImg}`)"
              :alt="`${prdData[num]['prdInfo']['name']}`"
            />
            <!--   -->
            <video
              class="detail-video"
              v-if="prdData[num]['img']['file'] == 'mp4'"
              :src="require(`../../assets/image/prd/detail/${detailImg}`)"
              :alt="`${prdData[num]['prdInfo']['name']}`"
              autoplay
              loop
              muted
            />
          </div>
        </div>

        <!--    -->
        <div class="detail-prd-info-bx">
          <div class="detail-prd-info-inner">
            <div class="detail-ctg">{{ prdData[num]['cat']['cat2'] }}</div>
            <div class="detail-flex">
              <div class="detail-name">{{ prdData[num]['prdInfo']['name'] }}</div>
              <!--    -->
              <div class="detail-price" v-if="sizeClick[0] == 1">
                {{
                  Number(prdData[num]['prdInfo']['price'][0])
                    .toString()
                    .replace(/\B(?=(\d{3})+(?!\d))/g, ',')
                }}
              </div>
              <div class="detail-price" v-if="sizeClick[1] == 1">
                {{
                  Number(prdData[num]['prdInfo']['price'][1])
                    .toString()
                    .replace(/\B(?=(\d{3})+(?!\d))/g, ',')
                }}
              </div>
            </div>
            <ul class="detail-size">
              <li v-for="(sizeImg, i) in prdData[num]['img']['sizeImg']" :key="i">
                <div
                  class="detail-size-img-bx pointer"
                  @click="
                    () => {
                      if (i == 0) {
                        sizeClick[0] = 1;
                        sizeClick[1] = 0;
                      } else {
                        sizeClick[0] = 0;
                        sizeClick[1] = 1;
                      }
                    }
                  "
                >
                  <img
                    class="detail-size-img"
                    :class="sizeClick[i] == 1 ? 'size-show' : ''"
                    :src="require(`../../assets/image/prd/size/${sizeImg}`)"
                  />
                </div>
                <div class="detail-size-txt-bx">
                  <span class="detail-size-txt">{{ prdData[num]['prdInfo']['size'][i] }}</span>
                </div>
              </li>
            </ul>
            <div class="detail-desc">{{ prdData[num]['prdDetail']['desc'] }}</div>
            <div class="detail-summary">{{ prdData[num]['prdDetail']['summary'] }}</div>

            <!--    -->
            <div
              class="cart-btn-bx"
              @click="
                () => {
                  if (sizeClick[0] == 1) {
                    price = prdData[num]['prdInfo']['price'][0];
                    quantity[0]++;
                    prdQuantity = quantity[0];
                    id = prdData[num]['id']+'-0';
                  } else {
                    price = prdData[num]['prdInfo']['price'][1];
                    quantity[1]++;
                    prdQuantity = quantity[1];
                    id = prdData[num]['id']+'-1';
                  }
                  name = prdData[num]['prdInfo']['name'];
                  cartImg = prdData[num]['img']['thumbnail'];
                  $emit('openCart');
                  addCart();
                }
              "
            >
              <button class="cart-btn pointer"> </button>
            </div>

            <!-- ...? -->
            <div class="info-wrap">
              <!--    -->
              <div
                class="info-bx pointer"
                @click="
                  () => {
                    infoCount[0]++;
                    if (infoCount[0] % 2 == 1) {
                      infoClick[0] = 1;
                    } else {
                      infoClick[0] = 0;
                    }
                  }
                "
              >
                <div class="info-subject-bx">
                  <span class="info-subject">    </span>
                  <span class="info-arrow" v-if="infoClick[0] == 0">+</span>
                  <span class="info-arrow" v-if="infoClick[0] == 1">-</span>
                </div>
              </div>
              <div class="info-txt" :class="infoClick[0] == 1 ? 'info-show' : ''">
                             .
                    .
                <br />
                <br />
                *         3,000 
                <br />
                *   2% 
                <br />
                *   5,000P  (1     )
              </div>

              <!--  &   -->
              <div
                class="info-bx pointer"
                @click="
                  () => {
                    infoCount[1]++;
                    if (infoCount[1] % 2 == 1) {
                      infoClick[1] = 1;
                    } else {
                      infoClick[1] = 0;
                    }
                  }
                "
              >
                <div class="info-subject-bx">
                  <span class="info-subject">  &  </span>
                  <span class="info-arrow" v-if="infoClick[1] == 0">+</span>
                  <span class="info-arrow" v-if="infoClick[1] == 1">-</span>
                </div>
              </div>
              <div class="info-txt" :class="infoClick[1] == 1 ? 'info-show' : ''">
                30,000     ,
                <br />
                <span> 1-2   .</span>
                <br />
                <br />
                         .    ,
                     .
                <br />
                - : <a href="https://www.cjlogistics.com/ko/tool/parcel/tracking" target="_blank">CJ</a>
                <br />
                <br />
                *          .
                <br />
                *        .
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  name: 'Detail',
  data() {
    return {
      num: this.$route.params.id,
      infoClick: [0, 0],
      infoCount: [0, 0],
      sizeClick: [1, 0],
      name: '',
      price: '',
      cartImg: '',
      id: '',
      quantity: [0, 0],
      prdQuantity: '',
      localSet: [],
      localGet: [],
    };
  },
  props: {
    prdData: Array,
    cartLocal: Array,
  },
  methods: {
    addCart() {
      let localData = {
        name: this.name,
        price: this.price,
        quantity: this.prdQuantity,
        img: this.cartImg,
        id: this.id,
      };

      if (localStorage.getItem('cart') == null) {
        this.localSet.push(localData);
        localStorage.setItem('cart', JSON.stringify(this.localSet));
      } else {
        this.localGet = JSON.parse(localStorage.getItem('cart'));
        if (
          this.localGet.findIndex((a) => {
            return a['name'] == this.name && a['price'] == this.price;
          }) == -1
        ) {
          this.localGet.push(localData);
          localStorage.setItem('cart', JSON.stringify(this.localGet));
        } else {
          this.localGet[
            this.localGet.findIndex((a) => {
              return a['name'] == this.name && a['price'] == this.price;
            })
          ]['quantity']++;
          localStorage.setItem('cart', JSON.stringify(this.localGet));
        }
      }
      let newQuantity = 0;
      let newTotal = 0;
      JSON.parse(localStorage.getItem('cart')).forEach((a) => {
        return (newQuantity = newQuantity += a['quantity']), (newTotal = newTotal += a['price'] * a['quantity']);
      });
      this.$emit('cartQuantity', newQuantity);
      this.$emit('cartLocal', JSON.parse(localStorage.getItem('cart')));
      this.$emit('total', newTotal);
    },
  },
  mounted() {
    //   
    window.scrollTo(0, 0);
  },
};
</script>

<style>
@import '../../assets/css/sub/detail.css';
</style>
