<template>
  <main id="cont">
    <section class="login-client-pg">
      <div class="signUp-client-container">
        <div class="login-title-box">
          <h2 class="login-title-txt"></h2>
        </div>
        <form action="" class="login-client-form">
          <!--  -->
          <div>
            <div class="form-label-box">
              <label class="form-label" for="signUpName"></label>
            </div>
            <div>
              <input
                type="text"
                id="signUpName"
                class="form-input"
                placeholder=" "
                autofocus
                @change="
                  (e) => {
                    name = e.target.value;
                  }
                "
              />
            </div>
          </div>
          <!--  -->
          <div>
            <div class="form-label-box">
              <label class="form-label" for="signUpPhone"></label>
            </div>
            <div>
              <input
                type="tel"
                id="signUpPhone"
                class="form-input"
                placeholder="   (ex. 01012345678)"
                maxlength="14"
                @change="
                  (e) => {
                    phone = e.target.value;
                  }
                "
              />
            </div>
          </div>
          <!--  -->
          <div>
            <div class="form-label-box">
              <label class="form-label" for="signUpEmail"></label>
            </div>
            <div>
              <input
                type="email"
                id="signUpEmail"
                class="form-input"
                placeholder=" "
                @change="
                  (e) => {
                    email = e.target.value;
                  }
                "
              />
            </div>
          </div>
          <!--  -->
          <div>
            <div class="form-label-box">
              <label class="form-label" for="signUpPw"></label>
            </div>
            <div>
              <input
                type="password"
                id="signUpPw"
                class="form-input"
                placeholder=", ,  (8 )"
              />
            </div>
          </div>
          <!--  -->
          <div>
            <div class="form-label-box">
              <label class="form-label" for="signUpPwChk"> </label>
            </div>
            <div>
              <input
                type="password"
                id="signUpPwChk"
                class="form-input"
                placeholder=" "
                @change="
                  (e) => {
                    password = e.target.value;
                  }
                "
              />
            </div>
          </div>

          <!--  -->
          <div class="policy-check-box">
            <div class="policy-all-check">
              <!--   -->
              <div class="agree-all" @input="allChk()">
                <input type="checkbox" name="all-check" id="all-check" />
                <label class="chk-label" for="all-check"> .</label>
              </div>
            </div>
            <div class="policy-each-check" @input="eachChk()">
              <!--   -->
              <div class="check-field">
                <input type="checkbox" name="policy-age" id="policy-age" />
                <label class="chk-label" for="policy-age">()   14  .</label>
              </div>
              <div class="check-field">
                <input type="checkbox" name="policy-termsOfUse" id="policy-termsOfUse" />
                <label class="chk-label" for="policy-termsOfUse"
                  >() <a href="https://www.tamburins.com/kr/terms-and-conditions/" target="_blank"></a>
                  .</label
                >
              </div>
              <div class="check-field">
                <input type="checkbox" name="policy-privacy" id="policy-privacy" />
                <label class="chk-label" for="policy-privacy"
                  >() <a href="https://www.tamburins.com/kr/privacy-policy/" target="_blank"></a>
                  .</label
                >
              </div>
              <!--   -->
              <div class="check-field">
                <input type="checkbox" name="policy-privacy-optional" id="policy-privacy-optional" />
                <label class="chk-label" for="policy-privacy-optional"
                  >()
                  <a href="https://www.tamburins.com/kr/privacy-policy/" target="_blank"
                    >  </a
                  > .</label
                >
              </div>
              <div class="check-field">
                <input type="checkbox" name="policy-marketing" id="policy-marketing" />
                <label class="chk-label" for="policy-marketing"
                  >()    SMS,  .</label
                >
              </div>
            </div>
          </div>

          <!--  -->
          <div class="standard-btn-box">
            <button class="standard-btn pointer" type="submit" @click="joinClick()"></button>
          </div>
        </form>
      </div>
    </section>
  </main>
</template>
<script>
export default {
  name: 'SignUp',
  data() {
    return {
      name: '',
      phone: '',
      email: '',
      password: '',
      joinSet: [],
      joinGet: [],
    };
  },
  props: {},
  methods: {
    //  '-' 
    phoneChange() {
      event.target.value = event.target.value
        .replace(/[^0-9]/g, '')
        .replace(/(^02.{0}|^01.{1}|[0-9]{3,4})([0-9]{3,4})([0-9]{4})/g, '$1-$2-$3');
    },

    //   
    joinClick() {
      event.preventDefault();

      let joinData = { name: this.name, phone: this.phone, email: this.email, password: this.password };
      let signUpName = document.querySelector('#signUpName').value;
      let signUpPhone = document.querySelector('#signUpPhone').value;
      let signUpEmail = document.querySelector('#signUpEmail').value;
      let signUpPw = document.querySelector('#signUpPw').value;
      let signUpPwChk = document.querySelector('#signUpPwChk').value;
      let signUpInfo = { name: signUpName, phone: signUpPhone, email: signUpEmail, pw: signUpPw, pwChk: signUpPwChk };

      //    
      if (signUpInfo['name'] == '') {
        alert('  .');
      }

      //    
      else if (signUpInfo['phone'] == '') {
        alert('  .');
      }

      //    
      else if (signUpInfo['email'] == '') {
        alert('  .');
      }

      //    
      else if (signUpInfo['pw'] == '') {
        alert('  .');
      }

      //     
      else if (signUpInfo['pwChk'] == '') {
        alert('     .');
      } else {
        //  
        if (signUpInfo['pw'] !== signUpInfo['pwChk']) {
          alert(' .  .');
        }

        //  ,    
        else {
          let policyAge = document.querySelector('#policy-age');
          let policyTermsOfUse = document.querySelector('#policy-termsOfUse');
          let policyPrivacy = document.querySelector('#policy-privacy');

          //     
          if (policyAge.checked == false || policyTermsOfUse.checked == false || policyPrivacy.checked == false) {
            alert('    .');
          } else if (policyAge.checked == true && policyTermsOfUse.checked == true && policyPrivacy.checked == true) {
            if (localStorage.getItem('join') == null) {
              this.joinSet.push(joinData);
              localStorage.setItem('join', JSON.stringify(this.joinSet));

              alert(' .');

              //   
              // window.location.href = 'http://localhost:8080/login';
              this.$router.push({ path: '/login' });
            } else {
              this.joinGet = JSON.parse(localStorage.getItem('join'));
              if (
                this.joinGet.findIndex((a) => {
                  return a['email'] == this.email;
                }) == -1
              ) {
                this.joinGet.push(joinData);
                localStorage.setItem('join', JSON.stringify(this.joinGet));
              } else {
                alert('  .');
              }
            }
          }
        }
      }
    },

    //    
    allChk() {
      let a = document.querySelectorAll('.policy-each-check input[type="checkbox"]');
      for (let i = 0; i < a.length; i++) {
        if (event.target.checked == true) {
          a[i].checked = true;
        } else {
          a[i].checked = false;
        }
      }
    },

    //   (5)  
    eachChk() {
      let allChkBox = document.querySelector('.policy-all-check input[type="checkbox"]');
      let b = document.querySelectorAll('.policy-each-check input[type="checkbox"]');

      if (event.target.checked == false) {
        allChkBox.checked = false;
      } else {
        let isChecked = [];

        for (let i = 0; i < b.length; i++) {
          isChecked.push(b[i].checked);
        }

        if (
          isChecked.findIndex((a) => {
            return a == false;
          }) == -1
        ) {
          allChkBox.checked = true;
        }
      }
    },
  },

  mounted() {
    //   
    window.scrollTo(0, 0);
  },
};
</script>
<style>
@import '../../assets/css/sub/signUp.css';
</style>
